:root {
  --standard-font: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
  font-size: 20px;
}

* {
  box-sizing: border-box;
}

/* General */
body {
  margin: 0;
  padding: 0;
  font-family: var(--standard-font);
}

.container {
  display: flex;
  flex-wrap: wrap;
  min-height: 100vh;
}

.container>* {
  flex-grow: 1;
  flex: 1 1 0px;
}

.spacing {
  height: 5vh;
  display: block;
}

/* Headlines */
h1,
h2,
h3,
h4,
h5 {
  font-family: Futura, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
}

h1 {
  font-size: 2.2rem;
  padding: 0.5rem 0;
  margin: 0;
  text-transform: uppercase;
}

h5 {
  font-size: 0.7rem;
  font-weight: 100;
  line-height: 1.2rem;
}

.subheadline {
  letter-spacing: 5px;
}


/* Main Section */
.main {
  color: #463B23;
  font-weight: 300;
  letter-spacing: 1px;
}

.main>* {
  padding: 20px;
}

@media only screen and (min-width: 801px) {
  .main {
    height: 100vh;
    overflow: scroll;
  }

  .main>* {
    padding: 80px;
  }
}

.main .event-overview {
  background-color: #F7F2DB;
  border-bottom: 2px solid #010101;

}

.main p {
  line-height: 1.5rem;
}

/* Media Section */
.media {
  background-color: #D9E8F5;
  background-image: url("https://invitivo.com/30th/images/long.jpg");
  background-size: cover;
  background-position: center;
}

@media only screen and (max-width: 800px) {
  .media {
    order: -1;
  }

  .container div {
    flex: auto;
  }

  .media {
    background-color: #D9E8F5;
    background-image: url("https://invitivo.com/30th/images/wide.jpg");
    min-height: 50vh;
  }
}

/* RSVP Button */
#RSVP {
  display: block;
  background-color: #010101;
  padding: 20px 30px;
  color: white;
  position: fixed;
  bottom: 40px;
  left: 40px;
  border-radius: 1000px;
  white-space: pre;
  font-size: 1.2rem;
  font-weight: 600;
  cursor: pointer;
}

#enter-details {
  height: 100vh;
  width: 100vw;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(255, 255, 255, 0.8);
  display: none;
  max-height: 100%;
  overflow: scroll;
  /* display:  block; */
}

#form {
  font-family: "Work Sans", Helvetica, Arial, sans-serif;
  display: block;
  width: 800px;
  background-color: white;
  position: absolute;
  top: 50%;
  right: 50%;
  transform: translate(50%, -50%);
  padding: 40px;
  background: rgb(255, 255, 255);
  box-shadow: rgba(0, 0, 0, 0.4) 0px 4px 8px 0px;
  font-size: 20px;
  border-radius: 5px;
}

#form h5 {
  font-size: 1.3em;
  margin: 0;
  margin-bottom: 40px;
}

#form p {
  color: #333333;
  font-size: 0.75em;
  font-weight: 500;
  margin: 5px 0;
}

#form h5 {
  text-align: center;
}

#form input,
#form textarea {
  border: 1px solid rgb(221, 221, 221);
  display: block;
  font-family: inherit;
  height: 48px;
  opacity: 1;
  padding: 0px 0.6rem;
  width: 100%;
  appearance: none;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  border-top-width: 1px;
  margin-bottom: 15px;
}

#form textarea {
  padding: 0.8rem;
  min-height: 120px;
}

#form button {
  pointer-events: auto;
  box-sizing: border-box;
  background: transparent;
  border: none;
  cursor: pointer;
  font: inherit;
  overflow: visible;
  -webkit-font-smoothing: inherit;
  -webkit-appearance: none;
  -webkit-box-align: center;
  align-items: center;
  background-color: rgb(0, 90, 200);
  border-radius: 100px;
  color: white;
  display: flex;
  font-family: "Basis Grotesque", Helvetica, Arial, sans-serif;
  -webkit-box-pack: center;
  justify-content: center;
  margin: 0px;
  outline: none;
  text-align: center;
  user-select: none;
  white-space: nowrap;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  height: 48px;
  padding: 0px 24px;
  width: 100%;
}

#form #form-success {
  display: none;
  color: rgb(15, 108, 15);
  text-align: center;
  font-size: 1.2em;
  font-weight: 600;
  margin: 30px 0;
}

@media only screen and (max-width: 800px) {
  #form {
    width: 100%;
  }
}